.popup{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    border-radius: 10px;
    width:80vw;
    height: 70vh;
    z-index: 3;
   
    background-color: #121213;
    padding: 10px;
}
.close-button{
    position: absolute;
    right:1vw;
    top:1vh;
    color: #ffffff;
    cursor: pointer;
}

.overlay{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 2;
    background-color: rgba(255,255,255,0.2);
}
.wordbox{
    position: relative;
    padding: 0;
   
    top:15vh;
   
    display: flex;
    align-items: center;
    display:grid;
    flex-grow: 0;
    
    grid-gap: 1vh;
    height: 50vh;
    grid-template-columns: auto auto auto auto auto;
    overflow: hidden;
    overflow-y: scroll;
    overflow-x: scroll;
   
    
}
.wordstyle{
    padding: 0;
    
    background-color: #4d8d55;
    display: flex;
    border-radius: 4px;
    flex-direction: row;
    justify-content: center;
    color: #ffffff;
    align-items: center;  
   
    
}