@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;900&display=swap');
    *{
        margin: 0;
        padding: 0;
    }
   #headTitle{
    display: flex;
       color: aliceblue;
       text-align: center;
       align-items: center;
       background-color: black;
       height: 10vh;
   }
   .game{
       display: flex;
       flex-direction: column;
       justify-content: center;
       align-items: center;
       background-color: black;
       height: 100vh;
       
   }
   .blankBoxes{
       width: 60vw;
       
   }
   
   .blankLettersGrid{
       display: flex;
       align-items: center;
       display:grid;
       grid-gap: 1vh;
       grid-template-columns: auto auto auto auto auto;
      
       height: 9vh;
       
       }

   .letterRow{
       background-color: #121213;
       display: flex;
       flex-direction: row;
       justify-content: center;
       height:8vh;
       width: 8vh;
       border: solid;
       border:1px solid;
       padding: 0;
       border-color: #7a7c7d;
       font-family: 'Roboto', sans-serif;
       font-size:large;
       color: #ffffff;
       align-items: center;  
   }
   
   .colorInstructions{
       display: flex;
       flex-direction: row;
       justify-content: center;
    height: 10vh;
    align-items: center;
    font-family: 'Roboto', sans-serif;
     
    color: white;
   }
   .yellowdot {
  height: 25px;
  width: 25px;
  background-color: #b69e4b;
  border-radius: 50%;
  display: inline-block;
}
.greendot {
  height: 25px;
  width: 25px;
  background-color: #4d8d55;
  border-radius: 50%;
  display: inline-block;
}
.blackdot {
  height: 25px;
  width: 25px;
  background-color: #3a3a3c;
  border-radius: 50%;
  display: inline-block;
}
.keyboard{
   width:40vw;
    }
    


.keyboardRow{
       display: flex;
       flex-direction: row;
       display: grid;
       height: 8vh;
       justify-content: center;
       grid-template-columns: auto auto auto auto auto auto auto auto auto auto;
       align-items: center;  
       grid-gap: 0.5vh;
       
   }
  


.keyboardkeys{
display: flex;
flex-direction: row;

font-weight: bold;
border: 0;
padding: 0;
background-color: #7a7c7d;
color:#ffffff;

height: 50px;
width: 30px;
border-radius: 4px;
justify-content: center;
align-items: center;
}

